import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import { addSentence } from '../actions/demoActions';
import { connect } from  'react-redux';

const styles = theme => ({
  container: {
    width: "100%",
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: "80%",
  },
});

const mapStateToProps = (state) => {
  return {
    input: state.demo.input,
    lang: state.demo.lang,
  }
}

const mapDispatchToProps = dispatch => ({
  addNewSentence: (sentence) => dispatch(addSentence(sentence)),
});

const InputSentences = (props) => {
  const { input } = props;
  return input.map((data, index) => {
    if (data.found) {
      const { extract, sentence } = data;
      const { startIndex, endIndex } = extract[0];
      return (
        <p key={`input${index}`}>
          {sentence.substring(0, startIndex)}
          <b>
            {sentence.substring(startIndex, endIndex + 1)}
          </b>
          {sentence.substring(endIndex + 1)}
        </p>
      )
    }
    return (<p key={`input${index}`}>{data.sentence}</p>)
  })
};

export class DemoBoard extends React.PureComponent {
  state = {
    'sentence': '',
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  onKeyPress = (event) => {
    const { addNewSentence } = this.props;
    if (event.key === 'Enter') {
      addNewSentence(this.state.sentence);
      this.setState({'sentence': ''});
    }
    return false;
  };

  render() {
    const { classes } = this.props;
    let { input, lang } = this.props;
    const title = lang === 'en' ? 'We extract Food related Keyword from your sentences' : '我們將會抽出句子中的食物相關詞語';
    const placeHolder = lang === 'en' ? 'Please input your sentence here' : '請輸入句子';
    return (
      <div className={classes.container}>
        <Typography variant="display1" component="h2" className={classes.textCen}>
          {title}
        </Typography>
        <TextField
          id="input"
          placeholder={placeHolder}
          value={this.state.sentence}
          className={classes.textField}
          onChange={this.handleChange('sentence')}
          onKeyPress={this.onKeyPress}
          margin="normal"
        />
        <InputSentences input={input} />
      </div>
    );
  }
}

const StyledDemoBoard = withStyles(styles)(DemoBoard);
export default connect(mapStateToProps, mapDispatchToProps)(StyledDemoBoard);


