import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import MenuIcon from '@material-ui/icons/Menu';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { connect } from  'react-redux';
import { resetDemo } from '../actions/demoActions'

const styles = {
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  menu: {
    top: 38,
  },
};

const mapStateToProps = (state) => {
  return {
    lang: state.demo.lang,
  }
}

const mapDispatchToProps = dispatch => ({
  resetDemo: () => dispatch(resetDemo()),
});

export class GameMenu extends React.PureComponent {
  state = {
    anchorEl: null,
  };

  handleMenuClick = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleMenuClose = () => {
    this.setState({ anchorEl: null });
  };

  demoReset = () => {
    this.handleMenuClose();
    this.props.resetDemo();
  };

  addButtons = (list) => {
    let buttonList =  [];
    for (let button of list) {
      if (button === 'Reset') {
        buttonList.push(<MenuItem key='Reset' onClick={this.demoReset}>Reset</MenuItem>);
      }
    }
    return buttonList;
  }
  render() {
    const { anchorEl } = this.state;
    const { classes, buttons, lang } = this.props;
    const title = lang === 'en' ? 'Key Phase Extraction Demo' : '關鍵字分析示範';
    return (
      <AppBar>
        <Toolbar>
          <IconButton className={classes.menuButton} color="inherit" aria-label="Menu">
            <MenuIcon onClick={this.handleMenuClick}/>
            <Menu 
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={this.handleMenuClose}
              className={classes.menu}
            >
              {this.addButtons(buttons)}
            </Menu>
          </IconButton>
          <Typography variant="title" color="inherit">
            {title}
          </Typography>
        </Toolbar>
      </AppBar>
    );
  }
}

GameMenu.propTypes = {
  classes: PropTypes.object.isRequired,
};

const StyledGameMenu = withStyles(styles)(GameMenu);
export default connect(mapStateToProps, mapDispatchToProps)(StyledGameMenu);


