import axios from 'axios';

export function addSentence(sentence) {
  return dispatch => {
    dispatch(extractKeyPhase(sentence));
  };
}

export function appendSentenceData(sentenceData) {
  return {
    type: 'ADD_SENTENCE',
    payload: sentenceData,
  }
}

export function extractKeyPhase(sentence) {
  return dispatch => {
    axios.get('https://westus.api.cognitive.microsoft.com/luis/v2.0/apps/1a6cd0fa-13f7-4329-be0b-fc2945ab157b', {
      params: {
      'subscription-key': 'a5e3a2d3381e430c860121f495480868',
      'timezoneOffset': -360,
      'q': sentence,
      }
    }).then((response) => {
      const { query, topScoringIntent, entities } = response.data;
      let found = null;
      if (topScoringIntent) {
        const { intent } = topScoringIntent;
        if (intent !== 'None')
          found = intent;
      }
      let foundList = null;
      if (entities) {
        foundList = entities.reduce((list, data) => {
          if (data.type === 'food') {
            list.push(data)
          }
          return list;
        }, []);
      }
      dispatch(appendSentenceData({sentence: query, found: found, extract: foundList}));
    }).catch((error) => {
      console.log(error);
    });
  };
}

export function resetDemo() {
  return dispatch => {
    dispatch({type: 'DEMO_RESET'});
  }
}

export function changeLang(lang) {
  return dispatch => {
    dispatch({type: 'CHANGE_LANG', payload: lang});
  }
}



