const initialState = {
  input: [],
  lang: 'zh',
};

export default function reducer(state= initialState, action) {
  switch (action.type) {
    case 'ADD_SENTENCE': {
      return {...state,
        input: [...state.input, action.payload],
      };
    }
    case 'CHANGE_LANG': {
      return {...state,
        lang: action.payload,
      }
    }
    case 'DEMO_RESET': {
      return initialState;
    }
    default:
      // do nothing
  }
  return state;
}



